.program-select__title {
  margin-top: 27px;
  margin-bottom: 37px;
}

.program-select__description {
  @include oswald-reg;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 39px;
}

.program-select__form-control {
  @include oswald-reg;
  text-transform: uppercase;
  font-size: 16px;
  color: rgba(68, 68, 68, 1);

  &--radio {
    color: rgba(34, 34, 34, 1);
  }

  &--checkbox {
    color: rgba(0, 0, 0, 1);
  }
}

input::placeholder {
  @include oswald-reg;
  font-size: 16px;
  text-transform: uppercase;
  color: rgba(68, 68, 68, 1);
}

.program-select__form-group-title {
  @include oswald-reg;
  font-size: 24px;
  line-height: 26px;
  color: rgba(0, 0, 0, 1);
}

.program-select__form-hint {
  text-align: center;
}

.program-select__filter-list {
  @include list-style;
}

.program-select__info-input {
  &--comment {
    resize: none;

    &::placeholder {
      @include oswald-reg;
      font-size: 16px;
      text-transform: uppercase;
      color: rgba(68, 68, 68, 1);
    }
  }
}
